@import "../public_func.sass";

.trans {
	transition: all 0.4s; }

.fix {
	position: fixed; }

header {
	line-height: rem(60);
	color: white;
	strong {
		float: left;
		font-weight: 300;
		margin-left: rem(10);
		font-size: rem(24);
		text-transform: uppercase;
		padding: 0 rem(20);
		background: #81A9DB; }
	.title {
		font-size: rem(40);
		line-height: inherit;
		text-transform: uppercase;
		background: #333333; } }
